import { FullModal } from '../../Modal'

export function AdminDeleteModal({ open, onClose, buttonOne, buttonTwo }) {
  return (
    <FullModal
      open={open}
      content=''
      onClose={onClose}
      buttonOne={buttonOne}
      buttonTwo={buttonTwo}
    />
  )
}

export function PermissionDeniedModal({ open, onClose }) {
  return (
    <FullModal open={open} content='' onClose={onClose} />
  )
}
